export default {
	data() {
		return {
			mydata:[],//收货地址数组
		}
	},
	methods: {
		// 初始化数据
		myajax:function(){
			var that=this
			uni.showLoading({
			    title: '加载中',
				mask:true
			});
			uni.request({
			    url: 'https://www.example.com/request', //仅为示例，并非真实接口地址。
			    data: {
			        text: 'uni.request'
			    },
			    success: (res) => {
			        console.log(res.data);
					that.mydata=res.data
			    }
			});
			setTimeout(function() {
				uni.hideLoading()
			}, 1000);
		},
		// 打开新页面
		opennew:function(id){
			uni.navigateTo({
				url: '../'+id+'/'+id
			});
			
		},
		// 删除收货地址
		godelete:function(index){
			uni.showModal({
			    title: '温馨提示',
			    content: '您确定删除此收货地址吗？',
				confirmColor:'rgb(0, 138, 5)',
			    success: function (res) {
			        if (res.confirm) {
			            console.log('用户点击确定');
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		}
	},
	onShow:function(){
		this.myajax()
	}
}
